import React from 'react'
import { Route, Switch, withRouter } from 'react-router-dom'

import Stores from '../stores/pages/index'
import Users from '../users/pages/index'
import Home from './pages/Home'
import Supplier from '../suppliers/pages/index'
import DistrbutionCenters from '../distributionCenters/pages/index'
import Fleet from '../fleet/pages/index'

const Routes = () => {
  return (
    <Switch>
      <Route exact path="/" component={Home} />
      <Route path="/users" component={Users} />
      <Route path="/stores" component={Stores} />
      <Route path="/suppliers" component={Supplier} />
      <Route path="/distrbutioncenters" component={DistrbutionCenters} />
      <Route path="/fleet" component={Fleet} />
    </Switch>
  )
}

export default withRouter(Routes)
